.about-box {
  background-color: $sea-salt-sherbet;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url(../images/about/mobile_milk@x1.png);
  background-size: 100vw;
  background-position-x: center;
  background-position-y: bottom 61px;
  background-repeat: no-repeat;

  &__header1 {
    font-family: $ff-alt;
    font-size: $fs-h2-mob;
    font-weight: 400;
    letter-spacing: $ls;
    color: $amelies-tutu;
    margin-bottom: 15px;
    margin-top: 125px;
    text-transform: uppercase;
  }
  &__header2 {
    font-family: $ff-alt;
    font-size: $fs-h3-mob;
    font-weight: 400;
    color: $blueberry-lemonade;
    margin-bottom: 26px;
    text-transform: uppercase;
    max-width: 200px;
    text-align: center;
    letter-spacing: $ls;
  }
& > div {
  display: flex;
  flex-direction: column;
  align-items: center;
}
  &__picture {
    margin-bottom: 46px;
    padding: 0 20px;
    display: block;
  }

  &__text {
    font-family: $ff-base;
    font-size: 1.2rem;
    font-weight: 500;
    color: $black;
    line-height: 19px;
    max-width: 480px;
    padding: 0 50px;
    text-align: left;

    &__p1 {
      margin-bottom: 47px;
      max-height: 79px;
    }

    &__p2 {
      margin-bottom: 10px;
      color: $gypsy-magic;
      & span {
        letter-spacing: 0.03em;
      }
    }

    &__p3 {
      margin-bottom: 17px;
      color: $gypsy-magic;
    }

    &__btn {
      font-family: $ff-base;
      font-weight: 700;
      font-size: $fs-base;
      line-height: 21px;
      width: 176px;
      height: 44px;
      background: $white;
      color: $blueberry-lemonade;
      border: none;
      border-radius: 22px;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

      &:hover,
      &:focus {
        background-color: #ecebeb;
      }

      &__icon {
        margin-left: 13px;
        fill: none;
        stroke: $blueberry-lemonade;
        font-size: 1.2rem;
        font-weight: $fw;
      }
    }
  }
}

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    .about-box {
      background-image: url(../images/about/mobile_milk@x2.png);
    }
  }
  @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
    .about-box {
      background-image: url(../images/about/mobile_milk@x3.png);
    }
  }
  @media (min-width: 768px) {
    .about-box {
      background-image: none;
      &__header1 {
        margin-top: 130px;
        font-size: $fs-h2-tab;
      }
      &__header2 {
        font-size: $fs-h3-tab;
        margin-bottom: 50px;
        max-width: none;
      }
      & > div {
        display: flex;
        flex-direction: row;
        gap: 17px;
        width: 100%;
        justify-content: center;
        align-items: center;
      }
      &__text {
        font-size: 1.4rem;
        line-height: 25px;
        margin-top: 50px;
        margin-bottom: 0;
        padding: 0;
        max-width: 340px;
        &__p1 {
          margin-bottom: 49px;
        }
        &__p2 {
          letter-spacing: $ls;
        }
        &__p3 {
          letter-spacing: $ls;
        }
        &__btn {
          margin-top: 62px;
          font-size: 16px;
        }
      }
      &__picture {
        padding: 0;
        margin-left: 39px;
        margin-bottom: 100px;
      }
    }
  }
  @media (min-width: 1280px) {
    .about-box {
      background-image: url(../images/about/desktop_milk@x1.png);
      background-size: 100vw;
      background-repeat: no-repeat;
      background-position-x: center;
      background-position-y: 342px;
      &__header1 {
        margin-top: 125px;
        font-size: $fs-h2-desk;
      }
      &__header2 {
        font-size: $fs-h3-desk;
        margin-bottom: 55px;
        max-width: 534px;
      }
      & > div {
        gap: 74px;
      }
      &__text {
        font-size: 14px;
        line-height: 28px;
        max-width: 463px;
        margin-top: 51px;
        letter-spacing: $ls;
        &__p1 {
          margin-bottom: 35px;
          font-size: 16px;
        }
        &__p2 {
          line-height: 27px;
          margin-bottom: 35px;
        }
        &__p3 {
          margin-bottom: 0;
        }
        &__btn {
          margin-top: 45px;
          margin-bottom: 107px;
        }
      }
      &__picture {
        margin: 0;
      }
    }
  }
  @media screen and (min-width: 1280px) and (min-device-pixel-ratio: 2),
    screen and (min-width: 1280px) and (min-resolution: 192dpi),
    screen and (min-width: 1280px) and (min-resolution: 2dppx) {
    .about-box {
      background-image: url(../images/about/desktop_milk@x2.png);
    }
  }
  @media screen and (min-width: 1280px) and (min-device-pixel-ratio: 2),
    screen and (min-width: 1280px) and (min-resolution: 288dpi),
    screen and (min-width: 1280px) and (min-resolution: 3dppx) {
    .about-box {
      background-image: url(../images/about/desktop_milk@x3.png);
    }
  }

