// feature/presentation
.presentation {
  background-color: $rosie;
  // max-width: 1200px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  &__head {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 40px;
  }

  &__group-name {
    font-family: $ff-alt;
    font-size: 10rem;
    font-weight: 400;
    color: $blueberry-lemonade;
    letter-spacing: 2;
    text-transform: uppercase;
    margin-bottom: 20px;
  }
  &__title {
    display: flex;
    flex-direction: column;
    width: calc(100% - 80px);
    font-family: $ff-alt;
    font-size: 5.8rem;
    color: $blueberry-lemonade;
    letter-spacing: 2;
    text-transform: uppercase;
    align-items: center;
    justify-content: center;
    margin-bottom: 40px;
    &--span {
      font-family: $ff-alt;
      font-style: 5, 8rem;
      color: $white;
      letter-spacing: 2;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0;
    }
  }

  &__button {
    font-family: $ff-base;
    font-weight: 700;
    font-size: 2rem;
    border: none;
    cursor: pointer;

    width: 560px;
    font-weight: 700;
    height: 47px;
    padding: 10px;
    background: $white;
    border-radius: 1.375rem;
    color: $blueberry-lemonade;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    margin-bottom: 100px;

    &:hover {
      background-color: $hover-background;
    }
  }

  &__team {
    font-size: 25px;
    margin-top: 50px;
    margin-bottom: 50px;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    max-width: 800px;
    row-gap: 25px;
    column-gap: 25px;
    text-align: center;
    align-items: center;
    justify-content: center;
  }
  &__team-members {
    display: flex;
    flex-direction: row;
    column-gap: 50px;
    // width: calc(100% - 20px);
  }

  &__team-members-name {
    margin-top: 30px;
    font-family: $ff-alt;
    font-size: 6rem;
    font-weight: 400;
    color: $blueberry-lemonade;
    letter-spacing: 2;
    text-transform: uppercase;
    margin-bottom: 20px;
    position: relative;
    display: flex;
    justify-content: center;
    &:before {
      content: '';
      position: absolute;
      background: url('/src/images/presentation_img/juli_works_trello.png') no-repeat center
        center/cover;
      width: 150px;
      height: 300px;
      border-radius: 10px;
      transform: rotate(-30deg);
      left: 730px;
      top: 380px;
    }
  }
  &__member-description {
    display: flex;
    flex-direction: row;
  }

  &__member-data {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
  }

  &__member-img {
    margin-bottom: 50px;
    margin-top: 100px;
    width: 300px;
    height: 450px;
    border-radius: 15px;
  }

  &__social-media {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__member-item {
    margin-left: 20px;
  }

  &__member-role {
    font-family: $ff-alt;
    font-size: 3rem;
    font-weight: 400;
    color: $blueberry-lemonade;
    letter-spacing: 2;
    margin-bottom: 10px;
  }

  &__member-item {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-height: 1000px;
    column-gap: 20px;
  }

  &__member-duties {
    font-family: $ff-alt;
    font-size: 2rem;
    color: $white;
    letter-spacing: 2;
    display: flex;
    text-transform: uppercase;
    align-items: center;
    justify-content: flex-start;
    margin: 0;
  }

  &__member-list {
    font-size: 20px;
    line-height: 1.5;
    margin-bottom: 20px;
  }

  &__member-about {
    max-width: 300px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      background: url('/src/images/presentation_img/juli_works.png') no-repeat center center/cover;
      width: 150px;
      height: 300px;
      border-radius: 10px;
      transform: rotate(30deg);
      top: 350px;
      right: -20px;
    }
  }
}

.social-list {
  list-style: none;
  display: flex;
  width: 150px;
  gap: 28px;
  margin-bottom: 30px;
  position: relative;
  //   &:before {
  //     content: '';
  //     position: absolute;
  //     background: url('/src/images/presentation_img/juli_works_adv.png') no-repeat center center/cover;
  //     width: 150px;
  //     height: 300px;
  //     border-radius: 10px;
  //     // transform: rotate(-deg);
  //     top: -300px;
  //     left: 750px;
  //   }

  &__icon {
    cursor: pointer;
    fill: $white;
    padding: 6.5px;
    background-color: $blueberry-lemonade;
    border-radius: 50%;
    width: 32px;
    height: 32px;
    justify-content: center;
  }
}
